import './styles.css';
import { css } from "@emotion/css";
import { Outlet } from "react-router-dom";
import $ from "jquery"
import React, { Component } from "react";
export default class App extends Component {
  componentDidMount () {
    $(window).on( 'scroll', function(){
      var scroll = $(window).scrollTop();
        //>=, not <=
       if (scroll >= 10) {
           $("#nv-nav").addClass("filter-white");
       } else {
           $("#nv-nav").removeClass("filter-white");
       }
   }); 
  }
  render() {
    return (
      <>
      <header id="nv-nav" className={css`position:fixed;width:100%;z-index:999;`}>
        <div  className={css`z-index:100;max-width:1400px;margin:auto;display:flex;height:60px;align-items:center;padding-left:14px;padding-right:14px;width:100%;`}>
            <h1 className={css`font-family:'Pacifico',cursive;margin:0;color:var(--accent);`}>NS.</h1>
            <div className={css`flex:1;`}></div>
            <div className={css`display:flex;align-items:center;gap:15px;`}>
              <a className="nav-links">About</a>
              <a className="nav-links">Work</a>
              <a className="nav-links">Contact</a>
            </div>
        </div>
      </header>
      <Outlet />
      </>
    )
  }
}
